<template>
  <div style="width:100%">
    <!-- 顶部toolbar -->
    <el-form ref="form1" :inline="true" class="toolbar" label-position="right" label-width="100px" :model="queryStr">
      <el-row :gutter="24">
        <el-form-item label="角色名称" class="ml15" prop="role_name">
          <el-input v-model.trim="queryStr.role_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click = "query(1)" >查询</el-button>
          <el-button type="primary" @click="handle_role" >新建</el-button>
        </el-form-item>
      </el-row>
    </el-form>
    <!-- 列表 -->
    <el-table :data="tableData" border v-loading="loading">
     <el-table-column type="index" width="50" label="序号">
      </el-table-column>
			<el-table-column header-align="center" align="center" label="操作"  min-width="150">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" @click = "edit_query(scope.row, scope.$index)">修改</el-button>
          <el-button type="danger" size="mini" @click = "del_query(scope.row, scope.$index)">删除</el-button>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="角色名称"  >
				<template slot-scope="scope">
					<span>{{scope.row.role_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="备注"  >
				<template slot-scope="scope">
					<span>{{scope.row.remark}}</span>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="系统类型"  >
				<template slot-scope="scope">
					<span>新物流</span>
				</template>
			</el-table-column>
    
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
    <!-- 新建工作流角色弹出框 -->
      <el-dialog :title="title1" :visible.sync="dialogFormOneVisible">
        <el-form label-width="120px" label-position="left" ref="form1" :rules="rules1" :model="form_one_data" >
          <el-form-item label="工作流角色" class="ml15" prop="role_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.role_name"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="备注" class="ml15" prop="remark">
            <el-col :span="12">
              <el-input v-model="form_one_data.remark"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="页面权限" class="ml15" prop="relation_company_name" >
            <el-col :span="12">
              <el-tree
                  :data="data2"
                  show-checkbox
                  ref="tree"
                  node-key="id"
                  :default-expanded-keys="[2, 3]"
                  :default-checked-keys="auth"
                  :props="defaultProps">
              </el-tree>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button type="primary"  @click="add_role">确 定</el-button>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  import {workRoleList,routeTree,addWorkRole,workRoleDel,workRoleDetail} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
        data(){
        return{
            title1:'新建角色',
            queryStr: {
                role_name:'',
                total: 1,       // 总数
                page: 1,        // 当前分页
                limit: 10,      // 每页条数
            },
            form_one_data:{
                role_name:'',
                remark:'',
                auth:[],
            },
            rules1: {
                role_name: [
                    {required: true, message: '请输入工作流角色', trigger: 'blur'},
                ],
            },
            auth:[],
            data2:[],//这是页面权限
            defaultProps: {
                children: 'children',
                label: 'route_name'
            },
            options:[],//选择国家数据
            dialogFormOneVisible: false,
            dialogTableVisible: false,  
            tableData:[],
            tableTwoData:[
                {
                    id:1,
                    actionTime:"张三",
                    username:"18600877033",
                    actionType:0,
                    remark:1,
                },
            ],
            loading: false,//loading
        }
        },
        mounted(){
            this.query()
        },
        methods:{
        // 查询
            query() { 

                let params =this.queryStr;
                workRoleList(params).then((res) => {
                this.total = Number(res.data.data.total);
                this.tableData = res.data.data.data;
                });
                
            },
            add_query(data,index){
            
            },
            handle_role(){
                this.title1="新建工作流角色";
                this.auth=[];
                this.form_one_data={
                    role_name:'',
                    remark:'',
                    auth:[],
                },
                routeTree({}).then((res) => {
                    this.data2 = res.data.data;
                });
                this.dialogFormOneVisible=true;
                
            },
            add_role(){
                this.$refs['form1'].validate((valid) => {
                    if (valid) {
                        this.dialogFormOneVisible = false;
                        this.form_one_data.auth = this.$refs.tree.getCheckedKeys();
                        let params = this.form_one_data;
                        addWorkRole(params).then((res) => {
                            this.form_one_data = {};
                            this.$message.success(res.data.msg);
                            this.query();
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });  
            },
            del_query(val,index){ //删除
                let id = val.id;
                let params = {
                'id':id,
                }
                workRoleDel(params).then((res) => {
                    this.$message.success('【删除】操作成功!');
                    this.query();
                });
            },
            // 修改 yjq
            edit_query(val, index){
                this.title1 = '编辑角色';
                    let id = val.id;
                    let params = {
                        'id':id,
                    }
                    workRoleDetail(params).then((res) => {
                    this.title1 = '编辑角色';
                    this.form_one_data = res.data.data;
                    this.auth = res.data.data.auth;
                    routeTree({}).then((res) => {
                            this.data2 = res.data.data;
                    });
                    this.dialogFormOneVisible = true;
                });
            },
            // 分页器
            currentChange(val) {
                this.queryStr.page = val;
                this.query();
            },
            // 时间戳的转换
            changeTime(time){
                let year = new Date((time)).getFullYear();
                let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
                let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
                let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
                let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
                let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
                return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
            }
        }
   }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}

  
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





