var URL_CONFIG = { // 各个环境下的接口
	// dev: {	// 开发环境
	// 	host: 'http://lgmanagerdev.aerohuanyou.com',
	// 	api: 'http://lgmanagerapidev.aerohuanyou.com'
	// },
	dev: {	// 开发环境
		host: '',
		api: 'http://m.com'
	},
	
}

let HTTPURL;
let currentHost = window.location.host;

for (let key in URL_CONFIG) {
	if ( currentHost === URL_CONFIG[key].host) {
		HTTPURL = URL_CONFIG[key].api;
	}
}

if(!HTTPURL) {
	HTTPURL = URL_CONFIG['dev'].api
}

export default HTTPURL;



