let base = 'http://mgapi.cnxin56.com';



// 登录
export const requestLogin = params => { return axios.post(`${base}/Site/doLogin`, params).then(res => res.data); };
// 登出
export const requestLoginOut = params => { return axios.post(`${base}/Site/loginOut`, params).then(res => res.data); };
// 修改密码
export const editPassword = params => { return axios.post(`${base}/Site/pwdModify`, params); };
// 七牛上传
export const qiniuToken = params => { return axios.post(`${base}/ShowMan/getUptoken`, params); };


//车型列表
export const carTypeList = params => { return axios.post(`${base}/busResource/BusTypeManage/busList`, params); };

//新建品牌车型
export const addCarTypes = params => { return axios.post(`${base}/busResource/BusTypeManage/addBus`, params); };
//删除车型
export const delBus = params => { return axios.post(`${base}/busResource/BusTypeManage/delBus`, params); };
//车型详情
export const detailBus = params => { return axios.post(`${base}/busResource/BusTypeManage/detailBus`, params); };

//知识库的数据类型树
